.dashboard {
  background-color: #e9ebee;
  display: flex;
  justify-content: left;
  align-items: center;
  max-width: 100%;
  height: 100vh;
  margin: 0;
}

.dashboard .swipe-container {
  background-color: #e9ebee;
  display: flex;
  flex: 3;
  max-width: 60%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}

.swipe {
  position: absolute;
}
.vertical-card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100%;
}

.dashboard .card-container {
  position: relative;
  width: 400px;
  height: 650px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-container {
  align-self: flex-start center;
}

.dashboard .swipe-info {
  margin: 0;
  z-index: 0;
}

.card {
  width: 400px;
  height: 650px;
  max-height: 80vh;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
  box-shadow: -5px 0px 7px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  object-fit: contain;
  align-items: center;
  
}

.cardContent {
  width: 100%;
  height: 100%;
}

.ch-name2{
  position: absolute;
  margin-bottom: 0;
  padding-bottom: 2%;
  padding-top: 10%;
  padding-left: 10%;
  bottom: 0;
  color: white;
  width: 100%;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  font-size: 20px;
  display: flex;
  gap: 10px;
}

.pet-name{
  position: absolute;
  margin-bottom: 10%;
  margin-left: 2%;
  padding-top: 2%; 
  padding-right: 5%; 
  padding-bottom: 1%;
  padding-left: 3%;
  bottom: 0;
  left: 0;
  color: white;
  border-radius: 10px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  font-size: 28px;
  text-transform: capitalize;
  color: rgb(238, 183, 44);
  display: flex;
  gap: 10px;
  transition: 0.3s ease;
}

.pet-name:hover {
  cursor: pointer;
  color: rgb(255, 183, 0);
  background-color: rgba(255, 255, 255, 0.251);
  transition: 0.3s ease;
}

.ch-name4{
  position: absolute;
  bottom: 0;
  right: 0;
  color: white;
  font-size: 18px;
  font-weight: bolder;
  text-transform: capitalize;
  background-color: rgba(20, 130, 255, 0.211);
  text-align: center;
  display: flex;
  padding: 1% 3%;
  border-radius: 10px;
  margin-bottom: 5%;
  margin-right: 5%;
}

.capitalize{
  text-transform: capitalize;
  text-transform: capitalize;
}

.loading-icon {
  margin-top: 40vh;
  margin-left: 45vw;
}

.icons-container {
  align-self: flex-end center;
}

.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  max-width: 100%;
  max-height: 100%;
  background: linear-gradient(180deg, #00000000, #000000da);
  border-radius: 10px;
}
.background-img {
  display: flex;
  align-items: center;
  justify-content: baseline;
  max-height: 50%;
  z-index: -1;
}

.sidebar-container{
  background-color: rgb(255, 255, 255);
  position: fixed;
  min-height: 100vh;
  height: 100vh;
  width: 110px;
  max-width: 10%;
  top: 0;
  right: 0;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.033);
  box-shadow: -3px 0px 5px 0px rgba(0, 0, 0, 0.033);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  word-break:break-all;
  transition: 0.3s;
}

.active{
  width: 500px;
  max-width: 25%;
  align-items: center;
  justify-content: space-around;
  transition: 0.3s;
  padding: 0px;
  overflow-y: scroll;
  flex-wrap: nowrap;

}