.icons {
  display: flex;
  width: 30vw;
  min-width: 450px;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.circle {
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
  box-shadow: -5px 0px 7px 0px rgba(0, 0, 0, 0.1);
  color: rgb(80, 80, 248);
  padding: 22px;
  border-radius: 50%;
  background-color: rgb(255, 255, 255);
  font-size: 24px;
}

.circle:hover {
  color: rgb(26, 26, 116);
  transition: 0.3s;
  font-size: 28px;
  cursor: pointer;
}
.x  {
  color: rgb(210, 2, 2);
  font-weight: bolder;
  padding-right: 28px;
  padding-left: 28px;
}

.heart{
    color: rgb(98, 201, 136)
}

.dog{
  color: rgb(65, 227, 249)
}
