.onboarding form {
  display: flex;
  justify-content: center;
}

.onboarding form section {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 30%;
  text-align: start;
}

.onboarding form input,
.onboarding form .url-label {
  padding: 15px 30px;
  margin: 10px 0;
  font-size: 14px;
  border: solid 2px rgb(219, 219, 219);
  border-radius: 10px;
}

.onboarding form .url-label {
  color: rgb(255, 255, 255);
  font-weight: bolder;
  background-color: rgba(67, 96, 126, 0.8);
  border: none;
  transition: 0.3s ease;
}

.onboarding form .url-label:hover {
  cursor: pointer;
  background-color: rgba(67, 96, 126, 1);
  transition: 0.3s ease;
}

.onboarding form input::-webkit-inner-spin-button,
.onboarding form input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin-top: 0;
}

.onboarding form label {
  margin: 10px 0;
}

.onboarding form .multiple-input-container {
  display: flex;
  flex-direction: row;
}

.onboarding form .multiple-input-container input {
  margin-right: 10px;
}

.onboarding form input[type="radio"],
.url-input {
  display: none;
}

.onboarding form input[type="checkbox"] {
  display: none;
}
.onboarding form .multiple-input-container label {
  padding: 10px;
  margin-right: 10px;
  border: solid 2px rgb(144, 144, 144);
  margin-right: 10px;
  transition: all 0.3s;
  border-radius: 10px;
}

.onboarding form input[type="radio"]:checked + label {
  border: solid 2px rgb(229, 31, 22);
}
.onboarding form input[type="checkbox"]:checked + label {
  border: solid 2px rgb(229, 31, 22);
}
.onboarding form input[type="submit"]:hover {
  background-color: rgb(235, 235, 235);
}

.onboarding form input[type="submit"]:active {
  background-color: rgb(226, 115, 115);
}

.onboarding form input[type="number"] {
  width: 33%;
}

.photo-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 15px 0px;
}

.profile-img {
  width: 500px;
  max-width: 90%;
  max-height: 400px;
  object-fit: contain;
  border-radius: 5px;
}

.profile-img-secondary{
  min-width: 80px;
  max-width: 80px;
  max-height: 80px;
  min-height: 80px;
  object-fit: cover;
  border-radius: 5px;
}

.secondary-images-div{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 650px;
  max-width: 100%;
  gap: 10px;
  margin-top: 20px;
  flex-wrap: wrap;
  
}
