@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&family=Readex+Pro:wght@200;300;400&display=swap');

html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
* {
  font-family: 'Nunito', sans-serif;
}

body {
  text-align: center;
  margin: 0;
}

.primary-title {
  font-weight: 700;
  font-size: 90px;
  color: white;
 
}

.primary-button {
  color: #fff;
  text-transform: uppercase;
  font-size: 15px;
  background: linear-gradient(45deg, #fe3072, #da381f);
  border-radius: 10px;
  border: none;
  padding: 12px 30px;
  transition: 0.6s;
  font-weight: 700;
}

.primary-button:hover {
  background: linear-gradient(260deg, #fe3072, #ff5940);
  cursor: pointer;
  transition: ease-in;
}


/* --------------------Home CSS-------------------------- */
.overlay-home{
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(238, 211, 204, 255);
  display: flex;
  flex-direction: column;
  align-items: center;

}
.background-img{
  display: flex;
  align-items: center;
  justify-content: baseline;
  max-height: 50%;
  z-index: -1;
}
