.ch-name3-div{
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    position: absolute;
    bottom: 0;
    left: 0;
    padding-bottom: 19%;
    width: 100%;
    gap: 10px;
    flex-wrap: wrap;
  }
  
  .ch-name3 {
    color: white;
    font-size: 15px;
    text-transform: capitalize;
    background-color: rgba(0, 0, 0, 0.211);
    text-align: left;
    display: flex;
    padding: 1% 3%;
    border-radius: 10px;
  }