.auth-modal {
    position: absolute;
    top: 300px;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    max-width: 360px;
    height: auto;
    background-color: white;
    border-radius: 10px;
    padding: 40px;
    left: 0;
    right: 0;
    text-align: center;
    
  }
  
  nav .nav-button:disabled {
    color: white;
    background: gray;
    transition: 0.4s;
    cursor: default;
  }
  
  .close-icon {
    float: right;
  }
  
  .close-icon:hover {
    cursor: pointer;
    font-weight: 700;
  }
  
  .secondary-button {
    color: gray;
    background-color: rgb(255, 255, 255);
    padding: 12px 30px;
    border: solid 2px gray;
    border-radius: 15px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 15px;
    transition: 0.4s;
    font-weight: 700;
  }
  
  .secondary-button:hover {
    color: black;
    border: solid 2px rgb(2, 2, 2);
    transition: 0.5s;
    cursor: pointer;
  }
  
  .auth-modal form {
    display: flex;
    flex-direction: column;
  }
  .auth-modal input[type="password"],
  .auth-modal input[type="email"] {
    padding: 8px;
    margin: 8px;
    font-size: 17px;
  }
  
  .password-error {
    color: red;
    font-style: italic;
    font-size: small;
  }

  .auth-modal form label{
    display: flex;
    justify-content: right;
  }

  .hide {
    display: none;
  }
  
  .valid {
    color: limegreen;
    margin-left: 0.25rem;
  }
  
  .invalid {
    color: red;
    margin-left: 0.25rem;
  }
  .x-mark{
    font-size: 18px;
  }
  .x-mark:hover{
    color:rgb(66, 3, 3);
  }