.sidebar-icon {
  font-size: 26px;
  margin-top: 15px;
  color: rgb(27, 138, 217);
  border: solid 2px rgb(27, 138, 217);
  border-radius: 50%;
  padding: 10px;
  transition: 0.2s;
}

.sidebar-icon:hover {
  cursor: pointer;
  color: rgb(36, 162, 253);
  border: solid 2px rgb(36, 162, 253);
  transition: 0.07s;
}

.sidebar-pet-name {
  font-size: 16px;
  font-weight: bolder;
  margin: 10px 0px 0px 0px;
  color: white;
  border-radius: 5px;
  background-color: rgba(27, 138, 217, 0.792);
  padding: 0px 10px;
}

.title-sidebar {
  margin-bottom: 5px;
  color: rgb(57, 57, 57);
}

.hr-sidebar {
  background-color: rgb(27, 138, 217);
  height: 4px;
  border: 0;
  width: 12%;
  margin-top: 0;
}

.sidebar-activeSidebar-div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sidebar-card {
  width: 450px;
  max-width: 95%;
  height: 450px;
  max-height: 50vh;
  /* box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
  box-shadow: -5px 0px 7px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  background-size: cover; */
  background-position: center;
  display: flex;
  justify-content: center;
  object-fit: contain;
  align-items: center;
  overflow: hidden;
  position: relative;
}

.sidebar-card img {
  position: absolute;
  top: 0;
  left: auto;
  width: 100%;
  opacity: 0;
  max-height: 80%;
  object-fit:contain;
}

.pet-info-sidebar {
  width: 90%;
}

.tags-sidebar {
  display: flex;
  justify-content: center;
  gap: 10px;
  width: 100%;
}

.pet-tag-sidebar {
  font-size: 14px;
  font-weight: bolder;
  background-color: rgba(0, 0, 0, 0.716);
  padding: 5px 10px;
  border-radius: 10px;
  color: white;
}

.capitalize{
    text-transform: capitalize;
}

.arrow-icons-sidebar{
  position: absolute;
  bottom: 5%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5%;
  font-size: 30px;
  color: rgba(27, 138, 217, 0.786);
}

.arrow-icon-sidebar:hover{
cursor: pointer;
color: rgb(3, 61, 103);
transition: 0.4s ease;
}

.sidebar-card .activeSlide {
  opacity: 1;
  transform: translateX(0);
}
.sidebar-card .lastSlide {
  transform: translateX(-100%);
}
.sidebar-card .nextSlide {
  transform: translateX(100%);
}