.chat-container {
    background-color: white;
    flex:1;
    max-width: 25%;
    height: 100vh;
    text-align: left;
    z-index: 1;
    margin: 0;
    display: flex;
    flex-direction: column;
  }
  
  .chat-container-header {
    background-color: red;
    background-image:
      linear-gradient(
        #6439ff, #311b7f
      );
    flex:1.5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 2;
    max-width: 100%;
    flex-wrap: wrap;
  }
  
  
  .chat-container .option {
    border: none;
    background-color: rgba(255, 255, 255, 0);
    font-size: 20px;
    margin: 2px;
    padding: 10px;
  }
  
  .chat-container .option:disabled {
    border-bottom: gray;
  }
  
  .chat-container .profile,
  .log-out-icon, .profile-icon {
    display: flex;
    flex-wrap: wrap;
    padding: 15px;
    align-items: center;
    color: white;
    font-size: 20px;
  }
  
  .match-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100px;
    width: 100px;
    margin-right: 10px;
  }
  
  .match-card h4{
    color: #ffffff;
    background-color: #1d1243a4;
    margin-top: -20px;
    border-radius: 5px;
    padding: 0px 15px;
  
  }
  
  .img-container {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .img-container img {
    object-fit: contain;
    width: 100%;
    height: 100%;
    align-items: center;
    border-radius: 10px;
  }
  
  .profile .img-container{
    height: 50px;
   margin-right: 10px;
  }
  
  .profile .img-container img {
    object-fit: contain;
    width: 100%;
    align-items: center;
  }
  
  .chat-display {
    padding: 20px;
    flex:10;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    overflow-x: auto;
    max-width: 100%;
    
  }
  .chat-input{
    display: flex;
    flex-direction: column;
    text-align: left;
    flex:2.5;
    padding: 20px;
    margin: 0;
    
  }
  
  textarea {
    width: 100%;
    height: 80px;
    border: solid 2px;
    border-color: rgb(188, 188, 188);
  }
  
  .matches-display{
    display: flex;
    align-items: start;
    flex-wrap: wrap;
    justify-content: space-evenly;
    height: 75%;
    padding: 20px;
    overflow-y: auto;
  }
  
.loading{
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  


.option, .option-2:hover{
    cursor: pointer;
  }
  
  .single-chat{
    display: flex;
    border-radius: 8px;
    padding: 5px;
    margin-bottom: 15px;
    width: 100%;
    border: solid 1px #6439ff08;
    align-items: flex-start;
    justify-content: flex-start;
  
  }
  
  .single-chat p{
    overflow-wrap: break-word;
    word-wrap: break-word;
    hyphens: auto;
    max-width: 70%;
    min-height: 15px;
    flex: 4;
  }
  
  .chat-message-header{
    flex: 1;
  }
  
  .second-chat-header{
  background-color: none;
  flex:1;
  border-bottom: solid 0.5px #6439ff3e;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  
  }
  
  .second-chat-header .option{
    font-weight: 700;
  }
  
  .chat-message-header p {
    max-width: 100%;
    font-weight: 700;
    text-align: center;
    font-size: 14px;
    margin-top: -2px;
  }
  
  .chat-message-header img{
    width: 100%;
    margin-right: 10px;
  }

  .chat-header-icons{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0;
  }

  .log-out-icon:hover, .profile-icon:hover{
    cursor: pointer;
    font-size: 22px;
  }